body {
  font-family: 'Arial', sans-serif;
  background-color: #e8f5e9;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.main-page {
  text-align: center;
  background-color: #ffffff;
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  max-width: 500px;
  width: 100%;
}

.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
}

.app-icon {
  width: 100px;
  height: 100px;
  margin-bottom: 10px;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-10px);
  }
}

.main-page h1 {
  font-size: 32px;
  color: #388e3c;
}

.main-page h2 {
  color: #66bb6a;
  font-size: 24px;
  margin-bottom: 10px;
}

.main-page h3 {
  color: #4caf50;
  margin-bottom: 30px;
}

.exercise-buttons {
  display: flex;
  flex-direction: column;
  gap: 15px;
}

button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 15px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s, transform 0.2s;
  width: 200px;
  margin: 0 auto;
}

button:hover {
  background-color: #388e3c;
  transform: scale(1.05);
}

.app-icon {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #388e3c;
}